import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faGithub, faItchIo } from "@fortawesome/free-brands-svg-icons"

const iconMappings = {
  github: faGithub,
  "itch.io": faItchIo,
}

const buildLinks = links =>
  links.map(link => (
    <a href={link.link} key={link.name} className="mb-2">
      <FontAwesomeIcon icon={iconMappings[link.name.toLowerCase()]} size="2x" />
    </a>
  ))

const Project = props => (
  <div className="flex flex-row mt-2 border-solid border rounded">
    <div className="flex flex-col flex-grow p-4">
      <div className="text-lg">{props.title}</div>
      <div className="text-gray-600">{props.description}</div>
    </div>
    <div className="flex flex-col p-4">{buildLinks(props.links)}</div>
  </div>
)

export default Project
